.component-resourceresult {
    background-color: #FAF3CE;
    color: #0b0e21;
    letter-spacing: 1px;
    padding: 10px;
    border-radius: 10px;
    margin-top: 20px;
    margin-bottom: 30px;
    width: 80%;
}

.component-resourceresult li {
   color: #DE8A6A;
}

.component-resourceresult li a:hover {
    -webkit-transform: scale(1.00);
        -ms-transform: scale(1.00);
            transform: scale(1.00);
    cursor: pointer;
}





